
const mintimeAction = {
    methods: {
        startMintimeAction(params) {
            let opts = typeof params === 'object' ? params : { name: params };
            if(!opts.name) { return; }
            this.clearMintimeActionTimeout(opts.name);
            let now = (new Date()).getTime();
            let actionNameTime = this.getActionTimerName(opts.name);
            this[actionNameTime] = now;
            if(typeof opts.callback === 'function') {
                opts.callback();
            }
        },
        getActionTimerName(name) {
            return 'actionNamed'+name+'startedAt';
        },
        async endMintimeAction(params) {
            let opts = typeof params === 'object' ? params : { name: params };
            if(!opts.name) { return; }
            let finish = () => {
                if(typeof opts.callback === 'function') {
                    opts.callback();
                }
            };
            let now = (new Date()).getTime();
            let actionNameTime = this.getActionTimerName(opts.name);
            let startedAt = opts.timeVariable ? this[opts.timeVariable] : parseInt(this[actionNameTime]);
            startedAt = startedAt ? startedAt : 0;
            let diff = now - startedAt;
            let minTime = opts.minTime ? opts.minTime : 900;
            this.clearMintimeActionTimeout(opts.name);
            let timeToEvaluate = minTime - diff;
            if(timeToEvaluate > 0) {
                this[opts.name+'_tm'] = new Promise(resolve => {
                    setTimeout(function() {
                        finish();
                        resolve();
                    }, timeToEvaluate);
                });
                return this[opts.name+'_tm'];
            } else {
                finish();
            }
        },
        clearMintimeActionTimeout(name) {
            let tmName = name+'_tm';
            if(this[tmName]) { clearTimeout(this[tmName]); }
        }
    }
};

export default mintimeAction;
