import axios from 'axios';

import uid from './../utils/uid.js';
import queryString from 'query-string';


const http = {
    inited: false,
    timezoneOffset: 0,
    timezoneOffsetInited: false,
    baseUrl: 'http://944663-cr26114.tmweb.ru',
    init() {
        if (this.inited) { return; }
        http.setDefaults();
        this.inited = true;
        http.getTimezoneOffset();
    },
    removeTrailingSlash(s) {
        return s.replace(/\/$/, "");
    },
    buildLink(link, params) {
        let resultLink = link;
        if(params) {
            if(Object.keys(params).length) {
                let qs = queryString.stringify(params);
                if(qs && qs.length) {
                    resultLink += '?'+qs;
                }
            }
        }
        return resultLink;
    },
    setDefaults() {
        axios.defaults.headers.common = {
            'X-Requested-With': 'XMLHttpRequest',
            'Content-Type': 'application/json'
        };
        axios.defaults.baseURL = http.baseUrl;
    },
    getTimezoneOffset() {
        if(http.timezoneOffsetInited) { return http.timezoneOffset; }
        let currentOffset = (new Date()).getTimezoneOffset();
        let storageOffset = parseInt(localStorage.getItem('timezone_offset'));
        storageOffset = isNaN(storageOffset) ? 0 : storageOffset;
        if(currentOffset !== storageOffset) {
            // http.post('/user/set-timezone');
            localStorage.setItem('timezone_offset', currentOffset);
        }
        http.timezoneOffset = currentOffset;
        http.timezoneOffsetInited = true;
        return currentOffset;
    },
    async getFile(path) {
        let resp = await axios({ url: path, baseURL: '/' });
        if(resp) { return resp.data; }
        return null;
    },
    async get(path, args, additional) {
        http.init();
        let headers = {};
        headers['Content-Type'] = 'application/json';
        let resp;
        try {
            resp = await axios.get(path, {
                params: args,
                headers: headers
            });
        } catch (e) {
            let handled = await this.handleError(e);
            if(!handled) { return handled; }
            return (await http.get(path, args));
        }
        if (!resp) {
            return null;
        }
        if (resp.data) {
            return resp.data;
        }
        return null;
    },
    async post(path, args) {
        http.init();
        let headers = {};
        let resp;
        try {
            resp = await axios.post(path, args, {headers: headers });
        } catch (e) {
            let handled = await this.handleError(e);
            if(!handled) { return handled; }
            return (await http.post(path, args));
        }
        if (!resp) {
            return null;
        }
        if (resp.data) {
            return resp.data;
        }
        return null;
    },
    async file(path, args) {
        http.init();
        let headers = {
            'Content-Type': 'multipart/form-data',
        };
        let resp;
        try {
            resp = await axios.post(path, args, {
                headers: headers
            });
        } catch (e) {
            let handled = await this.handleError(e);
            if(!handled) { return handled; }
            resp = await axios.post(path, args, {
                headers: headers
            });
            return resp;
        }
        if (!resp) {
            return null;
        }
        if (resp.data) {
            return resp.data;
        }
        return null;
    },
    async handleError(e) {
        return null;
    }
};



export default http;
