import Model from './model';
import http from './../libs/http';
import isFieldBlank from './../utils/is-field-blank';
import numberHelpers from './../utils/number-helpers';
import currencies from './../libs/currencies';

const fields = {
    id: 0,
    art: '',
    year: '',
    polotno: '',
    title: '',
    season: '',
    favorite: false,
    country: '',
    code: '',
    img: '',
    __img: '',
    href: '',
    product_consist: '',
    product_style: '',
    __current_size: '',
    color: {
        name: '',
        code: ''
    },
    sizes: {
        
    },
    
};


class ProductModel extends Model {

    constructor(productItem) {
        super();
        this.setupModel({
            fields: fields,
            instance: productItem
        });
        this.__img = http.removeTrailingSlash(http.baseUrl)+productItem.img;
        this.__title = this.title ? this.title : 'Товар без наименования';
        this.__color = this.color.name && this.color.code;
        this.currentPrice = this.getClearPrice();
        this.recalculateHtmlProperties();
        this.setCurrentPrice(this.getFirstSize());
    }

    recalculateHtmlProperties() {
        let props = [];
        let pairs = [
            { key: 'product_style', label: 'Стиль', cls: 'properties-style' },
            { key: 'product_consist', label: 'Состав', cls: 'properties-consist' },
            { key: 'polotno', label: 'Полотно' },
            { key: 'season', label: 'Сезон' },
            { key: 'country', label: 'Производство' }
        ];
        for(let i = 0; i < pairs.length; i++) {
            let pair = pairs[i];
            if(isFieldBlank(this[pair.key])) { continue; }
            props.push({ label: pair.label, value: this[pair.key], cls: pair.cls ? pair.cls : 'properties-'+pair.key });
            if(props.length > 3) { break; }
        }
        this.html_properties = props;

        /*
        "polotno": "Супрем",
    "season": "весна-лето",
    "favorite": false,
    "country": "РОССИЯ",
         */
    }

    getFirstSize() {
        try {
            let sizeKeys = Object.keys(this.sizes);
            return sizeKeys[0];
        } catch(e) {
            return false;
        }

    }

    setCurrentPrice(sizeType) {
        let s = null;
        let oldPrice = null;
        let price = null;
        try {
            s = this.sizes[sizeType];
            let prices = s.price;
            let discounted = prices.retail && prices.sale;
            if(discounted) {
                oldPrice = prices.retail;
                price = prices.sale;
            } else {
                price = prices.sale;
            }
            this.__current_size = sizeType;
        } catch(e) {
            console.log(e);
        }

        if(price) {
            this.currentPrice.price = numberHelpers.float(price);
            this.currentPrice.oldPrice = numberHelpers.float(oldPrice);
            this.currentPrice.__price = currencies.renderCost(this.currentPrice.price);
            this.currentPrice.__oldPrice = currencies.renderCost(this.currentPrice.oldPrice);
        } else {
            this.currentPrice = this.getClearPrice();
        }
    }

    getClearPrice() {
        return {
            price: '',
            __price: '',
            oldPrice: '',
            __oldPrice: ''
        }
    }

}


export default ProductModel;
