<template>
    <div :class="['modal modal-product modal-product--outer', { 'is-active': isOpen }]" id="modal-product" aria-hidden="false">
        <div class="modal__overlay" tabindex="-1" data-micromodal-close="">
            <div class="modal__container" role="dialog" aria-modal="true" aria-labelledby="modal-1-title">
                <covering-preloader :active="isLoading"></covering-preloader>
                <button class="modal__close" @click="closeByClick($event)" ><i class="icon icon-close"></i></button>
                <div class="modal-product--error" v-if="globalError">
                    <div v-html="globalError"></div>
                    <div>
                        <button class="btn-fill-style" @click="reload()">Попробовать еще раз</button>
                    </div>
                </div>
                <template v-if="product">

                    <main class="modal__content">
                        <div class="modal__content__left-side">
                            <img draggable="false" :src="product.__img" alt="product-image" class="product-image">
                            <div :class="['product-btn favorite-link', { 'is-active': product.favorite }]" @click="toggleFavorite()"><i :class="['icon icon-heart_fill']"></i></div>
                        </div>
                        <div class="modal__content__right-side">
                            <div class="product-modal--top__content">
                                <div class="product-modal--top__content--top">
                                    <h2 class="product-title" v-html="product.__title"></h2>
                                    <span class="article" v-html="product.art ? 'АРТ '+product.art : '&nbsp;'"></span>
                                    <div class="product-price">
                                        <span class="price-title">Цена</span>
                                        <span class="oldprice" v-html="product.currentPrice.__oldPrice" v-if="product.currentPrice.oldPrice"></span>
                                        <span class="price" v-html="product.currentPrice.__price" v-if="product.currentPrice.price"></span>
                                    </div>
                                </div>
                                <div class="product-modal--top__content--bottom">
                                    <div class="product-properties">
                                        <div :class="['product-properties__item', htmlProp.cls]" v-for="htmlProp in product.html_properties">
                                            <span class="property-name" v-html="htmlProp.label"></span>
                                            <span class="property-value" v-html="htmlProp.value"></span>
                                        </div>
                                        <div class="product-properties__item properties-consist" v-if="product.product_consistance">
                                            <span class="property-name">Состав</span>
                                            <span class="property-value" v-html="product.product_consistance"></span>
                                        </div>
                                        <div class="product-properties__item properties-size" v-if="product.sizes">
                                            <span class="property-name">Размер</span>
                                            <div class="input-row">
                                                <div class="input-group" v-for="size, sizeIndex in product.sizes">
                                                    <label>
                                                        <input type="radio" @change="handleProductSizeChange()" name="size" :value="sizeIndex" v-model="product.__current_size">
                                                        <span v-html="sizeIndex"></span>
                                                    </label>
                                                </div>

                                            </div>
                                        </div>
                                        <div class="product-properties__item properties-color" v-if="product.__color">
                                            <span class="property-name">Цвет</span>
                                            <span class="property-value color-value"><span :style="{ backgroundColor: product.color.code }"></span>{{ product.color.name }}</span>
                                        </div>

                                    </div>
                                </div>


                            </div>
                            <div class="product-modal--bottom__content">
                                <div class="addform-group">
                                    <div class="input-group btn-group">
                                        <button type="submit" class="btn-fill-style" @click="addToCart()">Добавить в корзину</button>
                                        <a :href="product.href" class="more-info btn-outline-style">Подробнее о товаре</a>
                                    </div>
                                </div>
                            </div>


                        </div>
                    </main>
                </template>
            </div>
        </div>
    </div>
</template>
<style lang="scss">
    body.product-modal--visible {
        overflow: hidden;
        .modal-product--outer {
            opacity: 1;
            pointer-events: initial;
            .modal__container {
                transform: translateY(0);
            }
        }

    }
    .modal-product--outer {
        display: block !important;
        opacity: 0;
        pointer-events: none;
        transition: $transition;
        z-index: 9999;
        .modal-product--error {
            position: absolute;
            top: 0;
            left: 0;
            background: var(--clean);
            z-index: 2000;
            display: flex;
            align-items: center;
            justify-content: center;
            height: 100%;
            width: 100%;
            text-align: center;
            padding: 30px;
            flex-direction: column;
            & > div:first-child {
                margin-bottom: 20px;
            }
        }
        .covering-preloader {
            background: $clean;
            z-index: 2100;
        }
        .modal__container {
            transition: $transition;
            transform: translateY(80px);
            height: 595px;
            max-height: 100vh;
        }
        .favorite-link {
            cursor: pointer;
            &.is-active {
                color: var(--favorite);
                text-shadow: 0 0 1px rgb(0 0 0 / 33%);
                &:hover {
                    color: var(--favorite-active);
                }
            }
        }
        .modal__content {
            height: 100%;
            padding: 0 !important;
            margin-top: 0 !important;
        }
        .modal__content__left-side {
            user-select: none;
        }
        .modal__content__right-side {
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
        }
        .product-modal--top__content {
            flex-grow: 1;
           // display: flex;
          //  flex-direction: column;
           // justify-content: space-between;
        }
        .product-modal--content {

        }
        .product-properties__item {
            label {
                user-select: none;
            }
        }
    }

    @media(max-width: 768px) {
        .modal-product--outer {
            .modal__content {
                height: 100%;
                padding: 40px 20px 20px !important;
            }
            .product-properties__item {
                .input-row {
                    flex-wrap: wrap;
                }
            }
            .modal__container {
                min-height: 100vh;
            }
        }
    }
</style>
<script>
    import uid from './../../utils/uid';
    import numberHelpers from './../../utils/number-helpers';
    import ProductModel from './../../models/product-model';
    import mintimeActionMixin from './../../mixins/mintime-action-mixin';
    export default {
        mixins: [mintimeActionMixin],
        data() {
            return {
                isOpen: false,
                productId: 0,
                product: null,
                isLoading: false,
                globalError: ''
            }
        },
        mounted() {
            window._$productModal = this;
        },
        beforeUnmount() {
            this.clearMintimeActionTimeout('isLoading');
        },
        methods: {
            reload() {
                this.getProduct();
            },
            handleProductSizeChange() {
                this.product.setCurrentPrice(this.product.__current_size);
            },
            async addToCart() {
                if(!this.product) { return; }
                let resp = await this.$http.post('/add-product-to-cart', { id: this.product.id, size: this.product.__current_size });
                let defaultErrorMsg = 'Не удалось добавить товар в корзину, отсутствует соединение с сервером';
                if(resp) {
                    if(resp.result) {
                        this.$toast.info('Товар добавлен в корзину');
                    } else {
                        window.$dialog.error({ content: resp.msg });
                    }
                } else {
                    window.$dialog.error({ content: defaultErrorMsg });
                }
            },
            toggleFavorite() {
                if(!this.product) { return; }
                this.product.favorite = !this.product.favorite;
                this.$http.post('/set-product-favorite-status', { product_id: this.product.id, is_favorite: this.product.favorite });
                this.$toast.info(this.product.favorite ? 'Товар добавлен в закладки' : 'Товар убран из закладок');
            },
            async showProductById(id) {
                this.productId = numberHelpers.int(id);
                if(!this.productId) { return; }
                this.show();
                await this.getProduct(id);
            },
            show() {
                this.isOpen = true;
                document.body.classList.add('product-modal--visible');
            },
            hide() {
                this.isOpen = false;
                document.body.classList.remove('product-modal--visible');
            },
            closeByClick($event) {
                if($event.stopPropagation) {
                    $event.stopPropagation();
                }
                this.hide();
            },
            async getProduct() {
                this.isLoading = true;
                this.startMintimeAction('isLoading');
                let requestId = uid();
                this.lastRequestId = requestId;
                let resp = await this.$http.get('/showProd.php', { prod: this.productId });
                if(window.location.href.indexOf('localhost') !== -1) {
                    resp = require('./../../trash/product-item').default;
                }
               // resp = null;
                await this.endMintimeAction('isLoading');
                this.isLoading = false;
                if(requestId !== this.lastRequestId) { return; }
                let product = null;
                if(resp) {
                    if(Array.isArray(resp) && resp.length) {
                        product = new ProductModel(resp[0]);
                    }
                }
                if(product) {
                    this.setGlobalError(false);
                    this.product = product;
                } else {
                    this.setGlobalError('Не удалось получить данные товара, отсутствует соединение с сервером');
                }
            },
            setGlobalError(err) {
                this.globalError = err;
            }
        }
    }
</script>