import VueToastNotification from './vue-toast-notification.vue'
import { createComponent } from './helpers';

const vueToastNotification = {
    getParentContainer() {
        let p = document.querySelector('#notices');
        if (!p) {
            p = document.createElement('div');
            p.className = 'notices is-top';
            p.id = 'notices';
            document.body.appendChild(p);
        }
        return p;
    },
    open(options) {
        let message;
        if (typeof options === 'string') message = options;
        const defaultOptions = {
            message
        };
        const propsData = Object.assign({}, defaultOptions, {}, options);
        const instance = createComponent(VueToastNotification, propsData, this.getParentContainer());
        return instance;
    },
    success(message, options = {}) {
        return this.open(Object.assign({}, {
            message,
            type: 'success'
        }, options))
    },
    error(message, options = {}) {
        return this.open(Object.assign({}, {
            message,
            type: 'error'
        }, options))
    },
    info(message, options = {}) {
        return this.open(Object.assign({}, {
            message,
            type: 'info'
        }, options))
    },
    warning(message, options = {}) {
        return this.open(Object.assign({}, {
            message,
            type: 'warning'
        }, options))
    },
};

window.$toast = vueToastNotification;

export default {
    install: (app, options) => {
        app.config.globalProperties.$toast = vueToastNotification;
    }
}
