import httpPlugin from './../plugins/http-plugin';
import vueToastNotification from './../plugins/vue-toast-notification';
import loadingProgress from './../components/elements/loading-progress.vue';
import coveringPreloader from './../components/elements/covering-preloader.vue';
import generalUtils from './../utils/general-utils';

export default function(apps) {
    for(let app of apps) {
        app.use(vueToastNotification);
        app.use(httpPlugin);
        app.component('loading-progress', loadingProgress);
        app.component('covering-preloader', coveringPreloader);
    }


};