<template>
    <div>
        <span :class="['toast', `toast-${type}`, { 'is-active': isActive, 'is-hiding': isHiding }]" @click="onClick" v-html="message"></span>
    </div>
</template>
<style lang="scss">


    .notices {
        position: fixed;
        bottom: 0;
        left: initial;
        right: 0;
        padding: 10px;
        overflow: hidden;
        z-index: 555555;
        pointer-events: none;
        text-align: right;
        .toast {
            display: inline-flex;
            align-items: center;
            margin: rem(5) 0;
            box-shadow: 0 1px 4px rgba(0, 0, 0, 0.12), 0 0 6px rgba(0, 0, 0, 0.04);
            border-radius: 4px;
            pointer-events: none;
            color: var(--clean);
            cursor: pointer;
            padding: 7px 20px 8px;
            animation-name: fade-in-left;
            animation-duration: 0.4s;
            animation-fill-mode: forwards;
            animation-iteration-count: 1;
            animation-timing-function: ease;
            &.is-active {
                animation: none;
                pointer-events: auto;
            }
            &.is-hiding {
                animation-name: fade-in-left;
                animation-duration: 0.4s;
                animation-iteration-count: 1;
                animation-timing-function: ease;
                animation-fill-mode: forwards;
                animation-direction: reverse;
                pointer-events: none;
            }
            /*
            555
             */
        }

        // Colors
        .toast-success {
            background-color: var(--success);
        }


        .toast-info {
            background-color: var(--dark);
        }


        .toast-error {
            background-color: var(--danger);
        }


        // Individual toast position
        .toast {
            &.is-top, &.is-bottom {
                align-self: center;
            }
            &.is-top-right, &.is-bottom-right {
                align-self: flex-end;
            }
            &.is-top-left, &.is-bottom-left {
                align-self: flex-start;
            }
        }

        @media screen and (max-width: 768px) {
            padding: 0;
            position: fixed !important;
        }
    }

</style>
<script>

  export default {
    name: 'toast',
    props: {
      message: {
        type: String,
        required: true
      },
      type: {
        type: String,
        default: 'success'
      },
      duration: {
        type: Number,
        default: 3000
      },
      dismissible: {
        type: Boolean,
        default: true
      },
      onClose: {
        type: Function,
        default: () => {
        }
      },
      queue: Boolean,
    },
    data() {
      return {
        isActive: false,
        isHiding: false,
        parent: null,
      }
    },
    mounted() {
      this.showNotice();
    },
    beforeUnmount() {
      let keys = ['tm1', 'timer', 'tm2'];
      for(let key of keys) {
        if(this[key]) { clearTimeout(this[key]); }
      }
    },
    methods: {
      close() {
        clearTimeout(this.timer);
        this.isHiding = true;
        this.tm2 = setTimeout(() => {
          this.removeElement()
        }, 600);
      },
      removeElement() {
          this.$el.parentNode.remove();
      },
      showNotice() {

        this.tm1 = setTimeout(() => {
          this.isActive = true;
        }, 420);
        this.timer = setTimeout(this.close, this.duration);
      },
      onClick() {
        if (!this.dismissible) return;
        this.onClose.apply(null, arguments);
        this.close()
      }
    },
  }
</script>
