const numberHelpers = {
    anInt(a) {
        return !isNaN(a) && parseInt(a) == a;
    },
    anFloat(a) {
        return !isNaN(a) && parseFloat(a) == a;
    },
    float(a) {
        let b = parseInt(a);
        return isNaN(b) ? 0 : b;
    },
    int(a) {
        let b = parseInt(a);
        return isNaN(b) ? 0 : b;
    },
    format(n) {
        n = parseFloat(n);
        n = isNaN(n) ? 0 : n;
        return n.toLocaleString('ru-RU');
    },
    formatAlt(n) {
        n = parseFloat(n);
        n = isNaN(n) ? 0 : n;
        return n.toLocaleString('en-US');
    },
    s2(a) {
        if (a > 10) {
            return a;
        }
        a = a.toString();
        if (a.length < 2) {
            return '0' + a;
        }
        return a;
    },
    stringToNumber(v) {
        if (typeof v === 'undefined' || v === null) {
            return '';
        }
        return v.toString().replace(/[^0-9.]/g, '');
    },
    stringIsNumber(string) {
        return /^[0-9]+(.[0-9]+)?$/.test(string);
    },
    replaceNumbers(string, char) {
        let splitted = string.split('');
        for (let i = 0; i < splitted.length; i++) {
            if (!this.stringIsNumber(splitted[i])) {
                continue;
            }
            splitted[i] = char;
        }
        return splitted.join('');
    },
    randomInteger(min, max) {
        let rand = min - 0.5 + Math.random() * (max - min + 1);
        return Math.round(rand);
    },
    removeNonNumbers(string) {
        let splitted = string.split('');
        for (let i = 0; i < splitted.length; i++) {
            if (this.stringIsNumber(splitted[i])) {
                continue;
            }
            splitted.splice(i, 1);
            i--;
        }
        return splitted.join('');
    },
    normalizeFloat(value) {
        if (typeof value === 'string') {
            return value.replace(/(-?\d)[.,](-?\d)/g, function (match, fVal, sVal) {
                if (isNaN(parseInt(fVal))) {
                    fVal = 0;
                }
                if (isNaN(parseInt(sVal))) {
                    sVal = 0;
                }
                if (sVal < 0) {
                    sVal = 0;
                }
                if (fVal == 0 && sVal > 0) {
                    return [fVal, sVal].join('.');
                }
                else {
                    if (sVal <= 0) {
                        return fVal;
                    }
                    return [fVal, sVal].join('.');
                }
            });
        }
        return value;
    }
};


export default numberHelpers;
