<template>
    <div :class="['covering-preloader', { 'is-active': isActive || active }]">
        <loading-progress :size="size ? size : 'sm'"></loading-progress>
        <div class="covering-preloader-text" v-html="text" v-if="text"></div>
    </div>
</template>
<style lang="scss">
    .covering-preloader {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: transparentize($substrate-case-3, 0.4);
        display: flex;
        align-items: center;
        justify-content: center;
        opacity: 0;
        pointer-events: none;
        transition: $transition;
        z-index: 50;
        svg {
            position: relative;
            top: -#{spacing(1)};
        }
        svg.size-xs {
            top: rem(-5);
        }
        &.is-active {
            opacity: 1;
            pointer-events: initial;
        }
        .covering-preloader-text {
            position: relative;
            top: rem(-5);
            padding-left: rem(10);
        }
    }
</style>
<script>
    import mintimeActionMixin from './../../mixins/mintime-action-mixin.js';
    export default {
        props: ['active', 'text', 'size'],
        mixins: [mintimeActionMixin],
        data() {
            return {
                isActive: false
            }
        },
        beforeUnmount() {
            this.clearMintimeActionTimeout('show');
        },
        methods: {
            show() {
                this.clearMintimeActionTimeout('show');
                this.startMintimeAction({
                    name: 'show',
                    callback: () => {
                        this.isActive = true;
                    }
                });
            },
            hide() {
                this.endMintimeAction({
                    name: 'show',
                    minTime: 350,
                    callback: () => {
                        this.isActive = false;
                    }
                });
            },
            blight() {
                this.show();
                this.hide();
            }
        }
    }
</script>
