<template>
    <div :class="['v-dialog-wrapper', isVisible ? 'active': '', 'type--'+type]">

        <div class="v-dialog-inner">
            <div class="v-dialog-bg" @click="handleCloseClick()"></div>
            <div :class="['v-dialog', isVisible ? 'open' : '']">
                <template v-if="type === 'dialog'">
                    <div class="v-dialog-body">
                        <i class="icon icon-close" v-if="closeCallback" @click="handleCloseClick()"></i>
                        <div v-if="preloader" class="v-dialog-preloader-svg-outer">
                            <loading-progress></loading-progress>
                        </div>
                        <div class="v-dialog-content" v-else>
                            <div class="v-dialog-icon">
                                <i :class="icon"></i>
                            </div>
                            <div class="v-dialog-content-body">
                                <div class="v-dialog-title" v-html="title"></div>
                                <div class="v-dialog-text" v-html="content" v-if="content"></div>
                            </div>
                        </div>
                    </div>

                    <div class="v-dialog--buttons" v-if="showButtons">
                        <button :class="['btn', closeBtnClass]" v-html="closeText" @click="handleCloseClickBtn()"
                                v-if="closeText"></button>
                        <button :class="['btn', confirmBtnClass]" v-html="confirmText" @click="$confirmCallback()"
                                v-if="confirmText"></button>
                    </div>
                </template>
            </div>
        </div>
    </div>
</template>
<style lang="scss">

    .v-dialog-body {
        padding: 60px 30px 30px;
        position: relative;
        .icon-close {
            top: 10px;
            right: 10px;
            position: absolute;
            font-size: 18px;
        }
    }

    .v-dialog-preloader-svg-outer {
        text-align: center;
        position: relative;
        top: 1px;
        padding-bottom: 30px;
    }

    .v-dialog-wrapper {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        pointer-events: none;
        opacity: 0;
        transition: all 0.2s ease;
        z-index: 999999;
        &.type--input {
            .v-dialog {
                text-align: left;
                background: var(--clean);
                .corner-close {
                    top: rem(11);
                }
            }
        }
        &.active {
            opacity: 1;
            pointer-events: initial;
            .v-dialog {
                transform: translateY(0);
            }
        }
    }

    .v-dialog-bg {
        background: $overlay;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
    }


    .v-dialog-title {
        font-size: 20px;
        font-weight: 600;
        &:last-child {
            margin-bottom: 30px;
            padding: 25px 0;
        }
    }

    .v-dialog-text {
        font-size: 14px;
        color: var(--text);
        max-width: 300px;
        margin: 0 auto 0;
    }

    .v-dialog-inner {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .v-dialog {
        width: rem(500px);
        max-width: 86%;
        background: var(--substrate-case-5);
        border-radius: 4px;
        overflow: hidden;
        position: relative;
        z-index: 4;
        transform: translateY(80px);
        transition: all 0.3s ease;
    }

    .v-dialog--head {
        background: $clean;
        padding: 40px 20px 20px;
    }



    .v-dialog-content {
        display: flex;
        align-items: center;
        user-select: none;
    }

    .v-dialog-icon {
        font-size: 64px;
        color: var(--secondary);
        padding-right: 30px;
        $w: 94px;
        min-width: $w;
        max-width: $w;
    }



    .v-dialog--body-text {
        min-height: 60px;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .v-dialog--head-text {
        font-size: 20px;
        font-weight: 500;
    }

    .v-dialog--buttons {
        padding: 0 20px 20px;
        display: flex;
        justify-content: flex-end;
        .btn {
            margin-left: 5px;
            margin-right: 5px;
            display: inline-flex;
            width: auto;
            justify-content: center;
            min-width: rem(155px);
        }
    }

    .v-dialog--head-icon {
        font-size: rem(65px);
        width: rem(100px);
        height: rem(100px);
        border-radius: 100%;
        background: $clean;
        margin: 0 auto 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding-top: 5px;
    }

    .v-dialog-open {
        overflow: hidden;
    }

    .modal-input--body {
        padding: 40px 20px;
        .v-input-element--switch {
            justify-content: space-between;
        }
        .v-input-element--switch.is-solo.state-b .v-input-element--switch-text-left {
            color: var(--text);
        }
        .v-input-element--switch {
            height: 32px;
        }
    }

    @media(max-width: 400px) {
        .v-dialog--body {
            padding-left: 20px;
            padding-right: 20px;
        }
    }

</style>
<script>
    export default {
        data() {
            let opts = this.getDefaultOptions();
            let rOpts = Object.assign({}, opts);
            return rOpts;
        },
        beforeMount() {
            window.$dialog = this;
        },
        methods: {
            getDefaultOptions() {
                return {
                    icon: null,
                    title: null,
                    content: null,
                    confirmCallback: null,
                    confirmBtnClass: '',
                    confirmText: '',
                    closeCallback: null,
                    cancelCallback: null,
                    closeBtnClass: null,
                    closeText: '',
                    defaultCloseText: 'ОК',
                    process: false,
                    isVisible: false,
                    showButtons: true,
                    preloader: false,
                    lastSetTime: 0,
                    type: 'dialog',
                    textfields: null,
                    textareas: null,
                    radioOptions: null,
                    switches: null,
                    hasClose: true
                };
            },
            async $confirmCallback() {
                if (this.confirmCallback) {
                    await this.confirmCallback(this.collectFieldValues());
                    return;
                }
                this.hide();
            },
            collectFieldValues() {
                let fv = {
                    textfields: [],
                    textareas: [],
                    switches: []
                };
                if(this.textfields) {
                    for(let f of this.textfields) {
                        fv.textfields.push(f.model);
                    }
                }
                if(this.textareas) {
                    for(let f of this.textareas) {
                        fv.textareas.push(f.model);
                    }
                }
                if(this.switches) {
                    for(let f of this.switches) {
                        fv.switches.push(f.model);
                    }
                }
                return fv;
            },
            hide() {
                this.isVisible = false;
                document.body.classList.remove('v-dialog-open');
                this.hidingProcess = false;
                window.$gUtils.cltm(this, 'resetTm');
                this.resetTm = setTimeout(this.reset, 300);
            },
            reset() {
                let d = this.getDefaultOptions();
                delete d.isVisible;
                for(let prop in d) {
                    this[prop] = d[prop];
                }
            },
            async hideMinTime() {
                if (this.hidingProcess) {
                    return;
                }
                this.hidingProcess = true;
                let now = (new Date).getTime();
                let diff = 750 - (now - this.lastSetTime);
                if (diff > 50) {
                    setTimeout(this.hide, diff);
                    return new Promise(resolve => setTimeout(resolve, diff));
                } else {
                    this.hide();
                    return true;
                }
                return true;
            },
            hideTimeout() {
                if (this.hidingProcess) {
                    return;
                }
                this.hidingProcess = true;
                setTimeout(this.hide, 55);
            },
            showAction() {
                this.isVisible = true;
                document.body.classList.add('v-dialog-open');
            },
            show() {
                window.$gUtils.cltm(this, 'resetTm');
                this.showAction();
            },
            handleOpts(opts) {
                if (typeof opts.closeBtnClass === 'undefined') {
                    opts.closeBtnClass = 'btn-primary';
                }
                if (!opts.closeCallback) {
                    opts.closeCallback = this.hideTimeout;
                }
            },
            setOptsAction(opts) {
                let d = this.getDefaultOptions();
                delete d.isVisible;
                Object.assign(d, opts);
                for(let prop in d) {
                    this[prop] = d[prop];
                }
                this.lastSetTime = (new Date()).getTime();
            },
            setOpts(opts) {
                let now = (new Date).getTime();
                let diff = 750 - (now - this.lastSetTime);
                if (diff > 50) {
                    setTimeout(() => {
                        this.setOptsAction(opts)
                    }, diff);
                } else {
                    this.setOptsAction(opts);
                }
            },
            handleCloseClick() {
                if (typeof this.closeCallback === 'function') {
                    this.closeCallback(this.collectFieldValues());
                }
            },
            handleCloseClickBtn() {
                if(typeof this.cancelCallback === 'function') {
                    this.cancelCallback();
                    return;
                }
                this.handleCloseClick();
            },
            load() {
                this.setOpts({preloader: true, type: 'dialog', showButtons: false });
                this.show();
            },
            alert(opts) {
                if (!opts.closeBtnClass) {
                    opts.closeBtnClass = '';
                }
                this.handleOpts(opts);
                if (!opts.icon) {
                    opts.icon = '';
                }
                if (!opts.closeText) {
                    opts.closeText = 'ОК';
                }
                opts.type = 'dialog';
                opts.showButtons = true;
                this.setOpts(opts);
                this.show();
            },
            error(opts) {
                if (!opts.closeBtnClass) {
                    opts.closeBtnClass = 'btn-danger';
                }
                this.handleOpts(opts);
                if (!opts.icon) {
                    opts.icon = 'icofont icofont-fire-burn';
                }
                if (!opts.closeText) {
                    opts.closeText = 'ОК';
                }
                if (!opts.title) {
                    opts.title = 'Ошибка';
                }
                opts.isVisible = true;
                opts.type = 'dialog';
                opts.showButtons = typeof opts.showButtons === 'undefined' ? true : opts.showButtons;
                opts.preloader = false;
                this.setOpts(opts);
                this.show();
                this.blurActiveElement();
            },
            blurActiveElement() {
                if(document.activeElement) {
                    if(typeof document.activeElement.blur === 'function') {
                        document.activeElement.blur();
                    }
                }
            },
            confirm(opts) {
                if (typeof opts.closeBtnClass === 'undefined') {
                    opts.closeBtnClass = 'btn-outline';
                }
                if (typeof opts.confirmBtnClass === 'undefined') {
                    opts.confirmBtnClass = 'btn-info'
                }
                this.handleOpts(opts);
                if (!opts.icon) {
                    opts.icon = 'flaticon flaticon-questions-circular-button';
                }
                if (!opts.title) {
                    opts.title = 'Подтверждение';
                }
                if (!opts.confirmText) {
                    opts.confirmText = 'да';
                }

                if (!opts.confirmCallback) {
                    opts.confirmCallback = () => {
                    };
                }
                if (!opts.closeCallback) {
                    opts.closeCallback = this.hide;
                }
                if (!opts.closeText) {
                    opts.closeText = 'Отмена';
                }
                opts.type = 'dialog';
                opts.showButtons = true;
                this.setOpts(opts);
                this.show();
            }
        }
    }
</script>
